.popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  z-index: 9999;
  &__content {
    position: absolute;
    max-width: 800px;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 15px;
    border-radius: 5px;

    @include media('>tablet') {
      padding: 20px 25px;
    }
  }
  &__close {
    position: absolute;
    top: -8px;
    right: -8px;
    width: 16px;
    height: 16px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 50%;
    cursor: pointer;
    &:hover {
      .popup__close-line {
        &::before, &::after {
          background: red;
        }
      }
    }
    &-line {
      background: transparent;
      width: 16px;
      height: 2px;
      transition: .25s;
      &::before, &::after{
        content: '';
        position: absolute;
        background: #000;
        width: 16px;
        height: 2px; 
        transition: .15s;
      }
      &::before {
        transform: rotate(-45deg)
      }
      &::after {
        transform: rotate(45deg)
      }
    }
  }
  &--show {
    opacity: 1;
    visibility: visible;
    transform: scale(1.0);
    transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
  }
}