*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  max-height: 1000000px;
}

html,
body {
  min-height: 100vh;
  font-family: $base-font-sans-serif;
  overflow-x: hidden;
}

html {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(#000, 0);
}

body {
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

video,
iframe,
object,
embed {
  max-width: 100%;
  display: block;
}

.page {

  &__inner {
    margin: 0 auto;
    width: 100%;
    max-width: $max-content-width;
    padding: 0 $gap;
  }

  &--no-scroll {
    overflow: hidden!important;
  }

}

.text-no-wrap {
  white-space: nowrap;
}

.no-js {
  .display-none {
    display: none;
  }
}

._white-bg {
  background-color: #fff!important;
  transition: background-color 700ms;
}

.custom-scroll {
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
