@font-face {
  font-family: 'Graphik LC';
  src: url('../fonts/GraphikLC-Bold.eot');
  src: url('../fonts/GraphikLC-Bold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GraphikLC-Bold.woff2') format('woff2'),
  url('../fonts/GraphikLC-Bold.woff') format('woff'),
  url('../fonts/GraphikLC-Bold.ttf') format('truetype'),
  url('../fonts/GraphikLC-Bold.svg#GraphikLC-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Graphik LC';
  src: url('../fonts/GraphikLC-Regular.eot');
  src: url('../fonts/GraphikLC-Regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GraphikLC-Regular.woff2') format('woff2'),
  url('../fonts/GraphikLC-Regular.woff') format('woff'),
  url('../fonts/GraphikLC-Regular.ttf') format('truetype'),
  url('../fonts/GraphikLC-Regular.svg#GraphikLC-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}

