}

.btn--burger .burger__line {
  background-color: #FFFF00;
  content: "";
  display: block;
  height: 2px;
  left: 0;
  position: absolute;
  top: calc(50% - 1px);
  transform-origin: 50% 50%;
  transition: background-color .15s ease-in-out, top .15s .15s ease-out,  transform .15s linear;
  width: 18px;
}

.btn--burger .burger__line:before, .btn--burger .burger__line:after {
  background-color: #FFFF00;
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  transform-origin: 50% 50%;
  transition: background-color .15s ease-in-out, top .15s .15s ease-out,  transform .15s linear;
  width: 18px;
}

.btn--burger .burger__line:before {
  top: 5px;
}

.btn--burger .burger__line:after {
  top: -5px;
}

.btn--burger.burger--active span {
  background-color: transparent;
  transition: background .15s ease-out;
}

.btn--burger.burger--active span:before, .btn--burger.burger--active span:after {
  transition: top .15s ease-out, transform .15s .15s ease-out;
}

.btn--burger.burger--active span:before {
  top: 0;
  transform: rotate3d(0, 0, 1, -45deg);
}

.btn--burger.burger--active span:after {
  top: 0;
  transform: rotate3d(0, 0, 1, 45deg);
}

