// contacts styles
.vcard-wrapper {
  @include media('>desktop') {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
  }
}
.vcard {
  display: flex;
  align-items: flex-start;
  position: relative;
  margin: 0;
  padding: 0;
  color: $purple;
  // background: #f00;
  @include media('>desktop') {
    max-width: 50%;
  }
  &__img {
    flex: 0 0 8rem;

    @include media('>desktop') {
      flex: 0 0 16rem;
    }
  }
  &__description {}
  &__text {
    margin-bottom: 1.2rem;
    font-size: 1.6rem;
    line-height: 2rem;
    
    @include media('>desktop') {
      margin-bottom: 2rem;
      font-size: 2.1rem;
      line-height: (32/21);
    }
  }
  &__title {
    margin: 0 0 1rem;
    font-size: 2rem;

    @include media('>desktop') {
      margin: 0 0 2rem;
      font-size: 3.6rem;
    }
  }
  &__phone {
    color: #000;
    font-size: 2rem;

    @include media('>desktop') {
      font-size: 3.2rem;
    }
    &:hover {
      color: inherit;
    }
    &:before {
      z-index: 1;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      content: '';
    }
  }
  & + & {
    margin: 2em 0 0;

    @include media('>desktop') {
      margin: 0 0 0 $gap;
    }
  }
}
