// sprints styles
.sprints {
  color: $purple;

  .h1 {
    margin-bottom: 13px;

    @include media('>tablet') {
      margin-bottom:30px;
    }

  }

  &__lead {
    font-size: 1.6rem;
    line-height: 2.4rem;

    @include media('>tablet') {
      font-size: 2.1rem;
      line-height: 3.6rem
    }

  }

  &__list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 18px;
    padding-bottom: 65px;
    border-bottom: 6px solid $purple;
    width: 290px;
    &:nth-child(4) {
      border-top: 6px solid $purple;
    }

    &--first {
      background-image: url(../images/svg/sprint-triangle-mobile-1.svg);
      background-repeat: no-repeat;
      background-position: 50% 335px;
      padding-bottom: 60px;
      @include media('>465px') {
        padding-bottom: 85px;
        background-position: 50% 287px;
      }  
      @include media('>565px') {
        padding-bottom: 96px;
      }
      @include media('>tablet') {
        background-image: none;
        padding-bottom: 0;
        padding-top: 50px;
        padding-bottom: 115px;
      }
    }
    &--second {
      background-image: url(../images/svg/sprint-triangle-mobile-2.svg);
      background-repeat: no-repeat;
      background-position: 100% 18px;
      padding: 18px 62px 0 0;
      margin-bottom: 18px;
      border-bottom: 0;
      @include media('>465px') {
        padding-right: 50px;
        background-position: 100% 17px;
      }
      @include media('>tablet') {
        background-image: none;
        padding: 50px 0;
      }
    }
    &--three {
      background-image: url(../images/svg/sprint-triangle-mobile-3.svg);
      background-repeat: no-repeat;
      background-position: 100% 78px;
      padding: 18px 52px 45px 0;
      @include media('>465px') {
        padding-top: 0;
        padding-right: 48px;
        margin-top: 18px;
        background-position: 100% -4px;
      }
      @include media('>565px') {
        padding-right: 44px;
        background-position: 100% -7px;
      }
      @include media('>tablet') {
        background-image: none;
        padding: 50px 0;
        padding-bottom: 75px;
        padding-right: 68px;
      }
      @include media('>992px') {
        padding-right: 65px;
        justify-content: space-between;
      }
      @include media('>1200px') {
        justify-content: space-between;
        padding-right: 92px;
        padding-bottom: 100px;
      }
    }
    &--four {
      background-image: url(../images/svg/sprint-triangle-mobile-1.svg);
      background-repeat: no-repeat;
      background-position: 50% 287px;
      padding-bottom: 60px;
      @include media('>465px') {
        padding-bottom: 85px;
        background-position: 50% 287px;
      }
      @include media('>565px') {
        padding-bottom: 96px;
        background-position: 50% 263px;
      }
      @include media('>tablet') {
        background-image: none;
        padding-bottom: 0;
        padding-top: 50px;
        padding-bottom: 115px;
      }
    }

    @include media('>465px') {
      width: 433px;
    }

    @include media('>565px') {
      width: 100%;
    }

    // @include media('>tablet') {
    //   padding-top: 50px;
    //   padding-bottom: 115px;
    // }

    &--only-description {
      justify-content: flex-start;
      padding-top: 18px;
      padding-bottom: 18px;
      padding-left: 24px;
      @include media('>tablet') {
        padding-top: 30px;
        padding-left: 0;
        padding-bottom: 40px;
      }
    }
  }

  &__time {
    font-size: 2rem;
    font-weight: bold;

    @include media('>tablet') {
      font-size: 3.2rem;
    }

    &--mr20px {
      margin-right: 20px;
    }
  }

  &__info {
    font-size: 1.6rem;
    line-height: 2.1rem;

    @include media('>tablet') {
      font-size: 2.1rem;
      line-height: 3.2rem;
    }
  }

  &__description {
    position: relative;
    flex: 1 1 100%;
    padding: 15px;
    border: 6px solid $purple;
    font-size: 1.6rem;
    line-height: 2.4rem;

    @include media('>tablet') {
      flex: 0 1 445px;
      font-size: 2.1rem;
      line-height: 3.2rem;
      padding: 21px 25px;
    }
    @include media('>1200px') {
      padding: 25px 25px 54px 25px;
    }

    &--first {
      @include media('>tablet') {
        margin-right: 12%;
      }
    }

    &--second {
      @include media('>tablet') {
        flex: 0 1 445px;
      }
      @include media('>desktop') {
        flex: 0 1 536px;
      }
    }

    &--pink {
      border: 6px solid $pink;
      color: $pink;
      padding-top: 23px;
      padding-bottom: 45px;
    }

    h3 {
      margin-bottom: 10px;
      font-size: 2rem;
      line-height: 2.5rem;
      font-weight: bold;

      @include media('>tablet') {
        font-size: 3rem;
        line-height: 3.6rem;
      }
      
      @include media('>desktop') {
        font-size: 3.2rem;
      }
    }

  }

  &__title-time {
    font-weight: bold;
    font-size: 2rem;

    .sprints__description--pink & {
      color: $purple;
    }

    @include media('>tablet') {
      font-size: 2.8rem;
    }

    @include media('>desktop') {
      font-size: 3.2rem;
    }
  }

  &__list-title {

    margin-bottom: 5px;
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: $purple;

    p + & {
      margin-top: 10px;
    }

    @include media('>tablet') {
      font-size: 2.1rem;
    }
  }

  &__members {
    display: none;

    @include media('>tablet') {
      display: block;
    }

    &--second {
      margin-right: 7%;
      flex-shrink: 0;
      @include media('>desktop') {
        margin-right: 9%;
      }
      @include media('>1200px') {
        margin-right: 16%;
      }
    }
  }

  &__members-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: $purple;

    @include media('>tablet') {
      font-size: 1.9rem;
    }
    @include media('>desktop') {
      font-size: 2.1rem;
    }

    li + li {
      margin-top: 5px;
    }
  }

  &__laser {
    position: absolute;
    overflow: hidden;
    z-index: 4;

    &--1 {
      bottom: 17px;
      left: 0;
      height: 48px;

      @include media('>tablet') {
        width: 445px;
        height: 98px;
        bottom: 18px;
      }
      @include media('>desktop') {
        width: 445px;
      }
    }
    &--2 {
      position: relative;
      height: 48px;

      @include media('>tablet') {
        width: 110px;
        height: 186px;
      }
      @include media('>desktop') {
        width: 118px;
      }
      @include media('>1200px') {
        width: 92px;
      }
    }
    &--3 {
      height: 48px;
      top: 135px;
      right: 0;

      @include media('>tablet') {
        width: 465px;
        height: 267px;
      }
      @include media('>desktop') {
        width: 268px;
      }
      @include media('>992px') {
        width: 465px;
      }
      @include media('>1200px') {
        top: 50px;
        width: 628px;
      }
    }
    &--4 {
      bottom: 17px;
      left: 0;
      height: 48px;

      @include media('>tablet') {
        width: 445px;
        height: 98px;
        bottom: 18px;
      }
      @include media('>desktop') {
        width: 445px;
      }
    }
  }

  &__bg-triangle {

    &--1 {
      display: none;

      @include media('>tablet') {
        display: block;
        background: url(../images/svg/sprint-triangle-1.svg) no-repeat top right;
        width: 445px;
        background-size: contain;
        height: 98px;
      }
    }
    &--2 {
      display: none;

      @include media('>tablet') {
        display: block;
        background: url(../images/svg/sprint-triangle-2.svg) no-repeat top right;
        width: 92px;
        height: 186px;
      }
    }
    &--3 {
      display: none;

      @include media('>tablet') {
        display: block;
        background: url(../images/svg/sprint-triangle-3.svg) no-repeat top right;
        width: 465px;
        height: 267px;
        @include media('>992px') {
          width: 465px;
        }
        @include media('>1200px') {
          width: 628px;
        }
      }
    }
    &--4 {
      display: none;

      @include media('>tablet') {
        display: block;
        background: url(../images/svg/sprint-triangle-1.svg) no-repeat top right;
        width: 445px;
        background-size: contain;
        height: 98px;
      }
    }

    &-mobile {

      &--1 {
        background: url(../images/svg/sprint-triangle-mobile-1.svg) no-repeat top right;
        width: 100%;
        background-size: contain;
        height: 48px;

        @include media('>tablet') {
          display: none;
        }
      }

      &--4 {
        background: url(../images/svg/sprint-triangle-mobile-1.svg) no-repeat top right;
        width: 100%;
        background-size: contain;
        height: 48px;

        @include media('>tablet') {
          display: none;
        }
      }
    }
  }
}

.visible-mobile {
  display: block !important;

  @include media('>tablet') {
    display: none !important;
  }
}