// Normalize Styles
@import 'settings/variables';
@import 'plugins/custom-bootstrap';
@import 'settings/normalize';
@import 'settings/fonts';
@import '../../node_modules/include-media/dist/include-media';
@import '../../node_modules/slick-carousel/slick/slick.scss';
@import 'settings/typography';
@import 'settings/template';

// Import Modules
@import '../_modules/header/header';
@import '../_modules/contacts/contacts';
@import '../_modules/tickets/tickets';
@import '../_modules/sprints/sprints';
@import '../_modules/footer/footer';
@import '../_modules/popup/popup';

.container.container--header {
  max-width: 100%;
}

.row_row {
  z-index: 10000;
  position: fixed;
  top: 0;
  width: 100%;
  pointer-events: none;

  .col, .col-10 {
    height: 100vh;
    &:before {
      content: '';
      display: block;
      height: 100vh;
      background: rgba(red, .2);
    }
  }
}

:focus {
  //outline: .4rem solid $pink !important;
  outline: none;
  outline-offset: .4rem;
}

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

svg {
  fill: currentColor;
}

.btn {
  border: 0;
  padding: 0;
  background-color: transparent;
  display: block;
  &--burger {
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: inline-block;
    height: 12px;
    outline: none;
    padding: 0;
    pointer-events: initial;
    position: relative;
    vertical-align: middle;
    width: 18px;
    z-index: 10;
  
    .burger__line {
      background-color: $yellow;
      content: "";
      display: block;
      height: 2px;
      left: 0;
      position: absolute;
      top: calc(50% - 1px );
      transform-origin: 50% 50%;
      transition: background-color .15s ease-in-out, top .15s .15s ease-out,  transform .15s linear;
      width: 18px;
      &:before, &:after {
        background-color: $yellow;
        content: "";
        display: block;
        height: 2px;
        position: absolute;
        transform-origin: 50% 50%;
        transition: background-color .15s ease-in-out, top .15s .15s ease-out,  transform .15s linear;
        width: 18px;
      }
      &:before {
        top: 5px;
      }
      &:after {
        top: -5px;
      }
    }
  
    &.burger--active {
      span {
        background-color: transparent;
        transition: background .15s ease-out;
        &:before, &:after {
          transition: top .15s ease-out, transform .15s .15s ease-out;
        }
        &:before {
          top: 0;
          transform: rotate3d(0, 0, 1, -45deg);
        }
        &:after {
          top: 0;
          transform: rotate3d(0, 0, 1, 45deg);
        }
      }
    }
  }
}

.wrapper {
  &--overlay {
    &:after {
      content: '';
      display: block;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.3);
      z-index: 10;
    }
  }
}

.section {
  padding-top: 4rem; //9rem
  padding-left: .8rem;
  padding-right: .8rem;

  @include media('>desktop') {
    padding-top: 7rem;
  }
}

.page-for {
  margin-bottom: 10px;
  padding-top: 50px;
  overflow: hidden;
  @include media('>desktop') {
    padding-top: 90px;
  }

  &__inner {
    margin: 0 auto;
    padding: 0 $gap;
    width: 100%;
    max-width: $max-width;
  }

  &__message {
    margin: 0 auto;
    padding: 0 $gap;
    width: 100%;
    max-width: 1029px;
    color: $purple;
  }

  &__title {
    margin: 0 0 34px;
    color: inherit;
    font-size: 20px;
    font-weight: 700;
    line-height: 32px;

    @include media('>desktop') {
      font-size: 32px;
      line-height: 48px;
      margin-bottom: 27px;
    }
  }

  &__text {
    width: 100%;
    max-width: 510px;
    color: inherit;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;

    @include media('>desktop') {
      font-size: 21px;
      line-height: 32px;
    }



    p:nth-child(2) {
      margin-top: .5em;
    }

    p + p {
      margin-top: 1.5em;
    }

  }

  &__think {
    margin: 50px auto 70px;
    padding: 0 $gap;
    width: 100%;
    max-width: $max-width;
    @include media('>desktop') {
      margin-top: 0;
    }
  }

  @at-root .think {
    position: relative;
    display: flex;
    align-items: flex-end;
    max-width: 285px;
    height: 760px;
    @include media('>tablet') {
      max-width: 750px;
      height: 606px;
    }
    @include media('>desktop') {
      justify-content: flex-start;
      align-items: flex-end;
      max-width: 819px;
      height: 730px;
      //background: url(../images/svg/triangle2.svg) no-repeat right bottom;
      background-size: contain;
    }
    &__laser {
      position: absolute;
      top: 121px;
      right: 0;
      width: 822px;
      height: 633px;
      overflow: hidden;
      z-index: 4;
      @include media('>tablet') {
        top: -24px;
        right: -69px;
        width: 0;
        height: 0;
      }
      @include media('>desktop') {
        top: 100px;
        right: 0;
      }
      
    }
    &__bg-triangle {
      background: url('../images/svg/triangle2.svg') no-repeat;
      background-repeat: no-repeat;
      background-size: 100%;
      position: absolute;
      top: 0;
      right: 0;
      width: 820px;
      height: 633px;
      display: none;
      @include media('>tablet') {
        display: block;
      }
    }
    &__bg-triangle-mobile {
      background: url('../images/svg/triangle2-mobile.svg') no-repeat;
      background-repeat: no-repeat;
      background-size: 100%;
      position: absolute;
      top: 60px;
      right: 0;
      width: 286px;
      height: 216px;
      @include media('>tablet') {
        top: 0;
        width: 312px;
        display: none;
      }
    }
    &__background-image {
      background: url('../images/bg/bg-image-for-fink.png') no-repeat;
      position: absolute;
      top: -150px;
      right: -390px;
      width: 600px;
      height: 1000px;
      z-index: 1;
      @include media('>tablet') {
        top: -350px;
        right: -510px;
      }
      @include media('>desktop') {
        top: -225px;
        right: -435px;
      }
    }

    &__inner {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      padding: 32px 40px 32px 24px;
      width: 312px;
      height: 382px;
      border: 8px solid #F845A0;
      opacity: 1;
      background-color: #fff;
      z-index: 3;

      @include media('>tablet') {
        width: 442px;
      }
      @include media('>desktop') {
        padding: 20px 48px 58px 34px;
        max-width: percentage(445/824);
        //height: 380px;
      }
    }

    &__message {
      font-size: 20px;
      font-weight: bold;
      line-height: 48px;
      color: $purple;
      @include media('>tablet') {
        font-size: 32px;
        line-height: 48px;
      }
    }
  }

  &__by-ticket {
    margin: 0 auto 40px;
    padding: 0 $gap;
    width: 100%;
    max-width: $max-width;
    display: flex;
    justify-content: center;
    align-items: center;

    @include media('>desktop') {
      margin-bottom: 72px;
    }
  }
}

.persons {
  background-color: $purple;
  padding: 40px 0;

  @include media('>desktop') {
    padding: 75px 0 149px;
  }

  &__inner-section {
    margin: 0 auto;
    padding: 0 $gap;
    width: 100%;
    max-width: $max-width;
  }

  &__description {
    margin-bottom: 44px;

    @include media('>desktop') {
      max-width: 888px;
      width: 100%;
      margin: 0 auto 44px;
    }

  }

  &__title {
    margin-top: 0;
    text-align: left;
    color: #ffffff;
    font-family: $base-font;
    font-size: 30px;
    margin-bottom: 33px;

    @include media('>desktop') {
      font-size: 60px;
      text-align: center;
    }
  }

  &__paragraph {
    font-family: $base-font;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #fff;

    @include media('>desktop') {
      font-size: 21px;
      line-height: 36px;
    }
  }

  &__list {
    margin-bottom: 65px;

    @include media('>desktop') {
      margin-bottom: 75px;
    }

    @at-root .persons-list {
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
      align-items: flex-start;
      align-content: space-between;
      list-style-type: none;
      padding: 0;

      @include media('>desktop') {
        flex-flow: row nowrap;
        justify-content: space-around;
        margin: 0;
      }

      &__item {
        flex: 0 1;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: flex-start;
        margin: 0 20px;
        color: #fff;
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
        margin-bottom: 10px;

        @include media('>desktop') {
          font-size: 21px;
          margin: 0 15px 0 15px;
        }

        &:hover,
        &:active {
          color: $yellow;
        }
      }

      &__inner-img {
        position: relative;
        margin-bottom: 15px;
        width: 64px;
        height: 64px;
        border-radius: 50%;
        overflow: hidden;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          background-color: $yellow;
          opacity: 0;
          transition: opacity ease-out 200ms;

          .persons-list__item:hover & {
            opacity: .4;
          }

          .persons-list__item--active:hover &,
          .persons-list__item--active & {
            opacity: .4;
          }
        }

        @include media('>desktop') {
          width: 76px;
          height: 76px;
        }

      }

      &__img {
        display: block;
        width: 100%;
        height: auto;
      }

      &__name {
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;

        .persons-list__item--active & {
          color: $yellow;
        }

        @include media('>desktop') {
          font-size: 21px;
          line-height: 29px;
        }

      }

    }

  }

  &__info {

  }

  @at-root .person-info {

    &__list {
      position: relative;
    }

    &__item {
      display: flex!important;
      // justify-content: space-between;
      align-items: flex-end;
    }

    &__background-triangle {
      background: url('../images/svg/triangle3.svg') no-repeat;
      position: absolute;
      top: 235px;
      right: 460px;
      width: 654px;
      height: 210px;
      z-index: 1;
      display: none;
      @include media('>550px') {
        display: block;
        top: 142px;
        right: -55px;
        width: 465px;
      }
      @include media('>tablet') {
        display: block;
        top: 175px;
        right: -110px;
        width: 587px;
      }
      @include media('>desktop') {
        display: block;
        top: 175px;
        right: -425px;
        width: 816px;
      }
    }
    &__background-triangle-mobile {
      background: url('../images/svg/triangle3-mobile.svg') no-repeat;
      position: absolute;
      top: 100px;
      right: -11px;
      width: 292px;
      height: 192px;
      z-index: 1;
      @include media('>450px') {
        width: 312px;
        top: 144px;
        right: 98px;
      }
      @include media('>550px') {
        display: none;
      }
    }

    &__description {
      position: relative;
      flex: 0 1 444px;
      padding: 40px 20px 35px 16px;
      border: 8px solid $yellow;
      color: #fff;
      font-size: 21px;
      margin-top: 244px;
      max-width: 290px;
      z-index: 4;
      background-color: rgba(96,22,196,.8);

      @include media('>450px') {
        margin-top: 300px;
        max-width: 312px;
      }
      @include media('>550px') {
        margin-top: 256px;
        max-width: 315px;
      }
      @include media('>tablet') {
        position: static;
        margin-top: 328px;
        padding: 40px 28px 55px 28px;
        margin-left: 25px;
        max-width: 397px
      }
      @include media('>desktop') {
        position: static;
        margin-top: 349px;
        margin-left: 110px;
        max-width: 597px;
        padding: 40px 25px 55px 28px;
      }
    }

    &__title {
      margin-bottom: 13px;
      font-weight: bold;
      font-size: 28px;
      @include media('>tablet') {
        font-size: 32px;
      }
    }
    
