      }
      @include media('>tablet') {
        max-width: 500px;
      }
    }
    &__img{
      position: relative;
      right: -110px;
      @include media('>450px') {
        right: -82px;
      }
      @include media('>550px') {
        right: -332px;
      }
      @include media('>tablet') {
        right: -450px;
      }
      @include media('>desktop') {
        right: -600px;
      }
    }
    // &__inner-img {
    //   flex: 0 1 50%;
    // }
  }

  .slick-next,
  .slick-prev {
    z-index: 100;
    position: absolute;
    top: 30%;
    font-size: 0;
    width: 53px;
    height: 53px;
    border: 0;
    background: url(../images/svg/angle-left.svg) no-repeat center transparent;
    -webkit-background-size: contain;
    background-size: contain;
    cursor: pointer;
    padding: 0;
  }

  .slick-next {
    right: 0;
    transform: rotate(180deg);
  }

  .slick-prev {
    left: 0;
  }
}

