.buy-ticket {
  padding: $gap;
  border: 8px solid;
  background: #fff;
  color: $pink;
  &_filled {
    border-color: $purple;
    background: $purple;
    color: #fff;
  }

  @include media('>desktop') {
    padding: 3.8rem 4.8rem 7.5rem;
  }
  &__title {
    margin-bottom: 36px;
    font-size: 20px;
    line-height: 28px;
    font-weight: bold;
    color: inherit;

    @include media('>tablet') {
      margin-bottom: 2rem;
      font-size: 3.2rem;
      line-height: 6.5rem;
    }
    // .visible-desktop {
    //   display: none;

    //   @include media('>tablet') {
    //     display: inline-block;
    //   }
    // }
    span {
      display: block;

      @include media('>tablet') {
        display: inline;
      }
    }
  }
  &__inner {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    color: #000;
    .buy-ticket_filled & {
      color: #fff;
    }
  }
  &__qty {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    width: 9rem;

    @include media('>tablet') {
      justify-content: space-between;
      width: 12rem;
    }
  }
  &__num {
    margin-top: 8px;
    flex: 0 1 calc(100% - 35px);
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    font-size: 36px;

    @include media('>tablet') {
      font-size: 60px;
      margin-top: 0;
    }
  }
  &__qty-nav {
    flex: 0 1 12px;
    display: flex;
    flex-flow: column wrap;
    justify-content: flex-start;
    align-items: flex-start;

    @include media('>tablet') {
      flex: 0 1 15px;
      margin-top: 5px;
    }
  }
  &__plus-qty,
  &__minus-qty {
    padding: 9px;
    background: transparent;
    border: 0;
    font-size: 0;
    cursor: pointer;
    svg {
      display: block;
      width: 12px;
      height: 7px;
      stroke: #000;

      .buy-ticket_filled & {
        stroke: #fff;
      }

      @include media('>tablet') {
        width: 15px;
        height: 8px;
      }
    }
    &:disabled {
      cursor: default;
      svg {
        opacity: .2;
      }
    }
  }
  &__prop {
    flex: 1 1 100%;
    text-align: left;
    letter-spacing: -.2px;
    font-size: 16px;
    line-height: 40px;

    @include media('>tablet') {
      font-size: 21px;
    }
  }
  &__summ {
    margin-top: 6px;
    width: 140px;
    font-size: 60px;
    font-weight: 400;
    letter-spacing: -2.4px;

    @include media('>tablet') {
      margin-top: 0;
      width: 180px;
      flex-shrink: 0;
      flex-grow: 0;
    }
    .icon--ruble {
      display: inline-block;
      vertical-align: middle;
      line-height: 1.15;
      width: 21px;
      height: 28px;
      flex-shrink: 0;
      flex-grow: 0;

      @include media('>tablet') {
        width: 32px;
        height: 42px;
        margin-bottom: 4px;
      }
    }
  }
  &__value-summ {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;

    @include media('>tablet') {
      justify-content: space-between;
    }
  }
  &__value {
    margin-right: 15px;
    font-size: 36px;
    font-family: $base-font;
    font-weight: 400;
    letter-spacing: -2.4px;

    @include media('>tablet') {
      margin-right: 10px;
      font-size: 60px;
      letter-spacing: -1.44px;
    }
  }
  &__pay {
    margin-top: 20px;
    width: 100%;
    flex: 1 1 100%;

    @include media('>tablet') {
      margin-top: 0;
      flex: 0 1 auto;
      width: auto;
    }
    .btn {
      &--pay-ticket {
        border: .6rem solid $pink;
        width: 100%;
        display: flex;
        flex-flow: column wrap;
        justify-content: flex-start;
        vertical-align: middle;
        padding: 2.2rem 3.2rem;
        background-color: $pink;
        color: #fff;
        font-family: $base-font;
        cursor: pointer;
        transition: .25s;
        &:hover {
          background-color: #DB0A75;
          border-color: #DB0A75;
        }

        @include media('>tablet') {
          width: auto;
          padding: 2.7rem 3rem;
        }
        .buy-ticket_filled & {
          background: transparent;
          border-color: $yellow;
        }
      }
      &__pay {
        font-size: 26px;
        font-weight: 400;

        @include media('>tablet') {
          font-size: 34px;
        }
      }
      &__method {
        font-size: 16px;
        font-weight: 400;
        
        @include media('>tablet') {
          font-size: 21px;
        }
      }
    }
  }
}