// header styles
.page-header {
  padding-top: 42px;
  padding-bottom: 40px;
  // margin-bottom: 40px;
  background-color: $purple;
  overflow: hidden;
  @include media('>tablet') {
    // margin-bottom: 64px;
  }
  @include media('>desktop') {
    // margin-bottom: 90px;
    padding-top: 30px;
    padding-bottom: 105px;
    background-position: -185px 25px;
  }
  &__burger {
    position: relative;
    display: block;
    z-index: 12;
    @include media('>desktop') {
      display: none;
    }
  }
  &__navs {
    // margin: 0 auto;
    // padding: 0 $gap;
    // width: 100%;
    // max-width: $max-width;
    display: flex;
    align-items: center;
    margin-bottom: 145px;
    padding-left: 8%;
    justify-content: flex-end;
    z-index: 4;

    @include media('>tablet') {
      justify-content: flex-start;
      margin-bottom: 100px;
    }

    @include media('>desktop') {
      //   justify-content: flex-start;
      margin-bottom: 100px;
    }
  }
  &__navs-mobile {
    position: fixed;
    top: -100px;
    left: 0;
    padding: 0;
    width: 100%;
    background: $purple;
    transition: .35s;
    z-index: 999;
    &--active {
      top: 0;
    }
  }
  &__row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 15px 25px;
  }
  &__title {
    font-size: 24px;
    color: $yellow;
    text-transform: uppercase;
  }
  &__main-menu {
    @include media('>desktop') {
      flex: 1 1;
      display: flex;
      justify-content: flex-end;
    }
    &--active-mobile {
      background-color: #000;
      z-index: 11;
      .page-nav {
        top: 0;
      }
    }
  }
  &__social {
    display: none;

    @include media('>desktop') {
      //   flex: 0 1 auto;
      display: block;
      padding-left: 3%;
    }
  }
  &__preview {
    position: relative; // margin: 0 auto;
    // padding: 0 $gap;
    // width: 100%;
    // max-width: $max-width;
    // display: flex;
    // flex-flow: row nowrap;
    // justify-content: center;
    // align-items: center;
    @at-root .preview-event {
      position: relative;
      padding-top: 17px;
      @include media('>tablet') {
        padding-top: 15px;
      }
      @include media('>desktop') {
        padding-top: 0;
      }

      &__inner-section {
        position: relative;
        // max-width: 625px;
        padding: 0 24px 30px; // width: 100%;
        // flex: 0 1 625px;
        border: 12px solid $yellow;
        background-color: $purple;
        color: $yellow;
        font-weight: bold;
        z-index: 3;
        @include media('>phone') {
          // margin-left: 5px;
          width: 300px;
        }
        @include media('>tablet') {
          margin-left: -33px;
          width: auto;
        }
        @include media('>desktop') {
          padding: 0 50px 30px;
          margin-left: 0;
        }
      }

      &__background-image {
        position: absolute;
        background: url(../images/bg/header-bg.png) no-repeat;
        top: -428px;
        left: -318px;
        width: 950px;
        height: 820px;
        @include media('>tablet') {
          top: -315px;
          left: -635px;
          width: 630px;
        }
        @include media('>desktop') {
          width: 650px;
        }
      }

      &__title-img {
        margin-top: -32px;
        @include media('>tablet') {
          margin-top: -15px;
        }
        @include media('>desktop') {
          margin-top: 0;
        }
        svg {
          width: 100%;
        }
      }
      &__title-img--mobile {
        margin-top: 0;
        margin-left: -74px;
        svg {
          height: 18.5px;
          display: flex;
        }
      }

      &__descr {
        margin-top: -15px;
        margin-bottom: 40px;
        color: $yellow;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        @include media('>desktop') {
          margin-bottom: 65px;
          font-size: 21px;
        }
      }

      &__inner {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: flex-end;
      }

      &__date {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: flex-start;
      }

      &__num {
        font-size: 48px;
        line-height: 48px;
        margin-right: 8px;
        @include media('>desktop') {
          font-size: 69px;
          line-height: 69px;
        }
      }

      &__month-year {
        display: flex;
        flex-flow: column wrap;
        justify-content: flex-start;
        align-items: flex-start;
      }

      &__month {
        margin-top: 3px;
        font-size: 16px;
        font-weight: 700;
        @include media('>desktop') {
          font-size: 25px;
          margin-top: 10px;
        }
      }

      &__year {
        font-size: 16px;
        font-weight: 700;
        @include media('>desktop') {
          font-size: 25px;
        }
      }

      &__place {
        font-size: 16px;
        line-height: 32px;
        font-weight: bold;
        @include media('>desktop') {
          font-size: 21px;
        }
      }
      &__laser {
        position: absolute;
        top: -43px;
        left: 6px;
        width: 312px;
        height: 63px;
        overflow: hidden;
        z-index: 4;
        @include media('>tablet') {
          top: 0;
          left: -192px;
          width: 0;
          height: 376px;
        }
      }
      &__bg-triangle {
        position: absolute;
        top: 0;
        left: 0;
        background-image: url(../images/svg/triangle1.svg);
        background-repeat: no-repeat;
        background-size: 100%;
        width: 170px;
        height: 376px;
        display: none;
        @include media('>tablet') {
          top: 15px;
          display: block;
          width: 167px;
        }
        @include media('>desktop') {
          top: 0;
          width: 200px;
        }

        &--mobile {
          position: absolute;
          top: 0;
          left: 0;
          background-image: url(../images/svg/triangle1-mobile.svg);
          background-repeat: no-repeat;
          background-size: 100%;
          width: 304px;
          height: 63px;
          display: block;
          @include media('>tablet') {
            display: none;
          }
        }
        @include media('>desktop') {
          // bottom: 60px;
          // top: auto;
        }
      }
    }
  }
  &__age {
    position: absolute;
    top: -40px;
    right: 7px;
    font-size: 21px;
    color: $yellow;
    @include media('>tablet') {
      top: -37px;
      right: 38px;
    }
    @include media('>desktop') {
      bottom: 60px;
      top: auto;
    }
  }
}

@keyframes cirlce1 {
  from {
    cy: 0%;
    fill: none;
    stroke: grey;
  }
  to {
    cy: 50%;
    fill: #9284D9;
    stroke: white;
  }
}


.page-nav {
  position: fixed;
  top: -300px;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: $purple;
  margin: 0;
  padding: 50px 0 0 0;
  z-index: 9;
  list-style-type: none;
  transition: .25s;
  @include media('>desktop') {
    position: relative;
    top: 0;
    flex: 1 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 0;
    margin: 0;
    list-style-type: none;
    text-align: center;
  }
  &__item {
    flex: 1 1;
    margin-bottom: 25px;
    @include media('>desktop') {
      &:first-child {
        margin: 0;
      }
      margin: 0;
    }
  }
  &__link {
    display: block;
    font-size: 21px;
    font-weight: bold;
    color: $yellow;
    white-space: nowrap;
    &:hover {
      color: $white;
    }
  }
}

.list-social {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
  &__item {
    & + & {
      margin-left: 10px;
    }
  }
  &__link {
    display: block;
    font-size: 0;
    .icon {
      fill: $yellow;
    }
    @at-root .icon {
      display: block;
      width: 32px;
      height: 32px;
      .list-social__link:hover & {
        fill: $white;
      }
    }
  }
}
