//// Typography
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
.h {
  margin: 0;
  font-family: $headings-font-family;
  font-weight: bold;
  color: inherit;
}
.h {
 &._muted {
   color: #3d3d3d;
 }
 &_center {
    @include media('>desktop') {
      text-align: center;
    }
 }
}
h1,
.h1 {
  margin-bottom: 1em;
  font-size: 3rem;
  color: $purple;

  @include media('>desktop') {
    font-size: 6rem;
  }
}
h2,
.h2 {
  font-size: 2.4rem;
  line-height: 1.5;
}

