p {
  margin: 0;
  color: inherit;
  font-size: 1.6rem;
  line-height: 2.4rem;

  @include media('>tablet') {
    font-size: 1.6rem;
    line-height: 2.5rem;
  }
  &+& {
    margin-top: 1.5em;
  }
}

a {
  color: $dark-gray;
  text-decoration: none;
  &:active,
  &:hover {
    color: $brown-deep;
    svg {
      fill: $brown-deep;
    }
  }
  svg {
    fill: $dark-gray;
  }
}

